section.Common {
	width: 100%;
	* {
		// border: 1px solid blue;
	}
	.inner {
		width: 100%;
		margin: 0;
		padding: 0 5rem;
		.content {
			width: 100%;
			padding: 5rem;
			.text {
				.heading {
					font-weight: 700;
					font-style: italic;
					font-size: 4rem;
					text-transform: uppercase;
					outline: none;
					text-decoration: none;
					position: relative;
					// font-size: 8em;
					line-height: 1;
					// color:
                    //#9e9ba4;
                    cursor: pointer;
					display: inline-block;
				}
				.link--kukuri {
					text-transform: uppercase;
					font-weight: 900;
					overflow: hidden;
					line-height: 0.75;
					color: black;
				}

				.link--kukuri:hover {
					color: black;
					// transform: rotate3d(20);
					// transform: translate3d(-100%, 0, 0);
					// transition: transform 0.4s;
					transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
				}

				.link--kukuri::after {
					content: '';
					position: absolute;
					height: 16px;
					width: 100%;
					top: 50%;
					margin-top: -8px;
					right: 0;
					background: #f9f9f9;
					transform: translate3d(-100%, 0, 0);
					transition: transform 0.4s;
					transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
				}

				.link--kukuri:hover::after {
					transform: translate3d(100%, 0, 0);
				}

				.link--kukuri::before {
					content: attr(data-letters);
					position: absolute;
					z-index: 2;
					overflow: hidden;
					color: #424242;
					white-space: nowrap;
					width: 0%;
					transition: width 0.4s 0.3s;
				}

				.link--kukuri:hover::before {
					width: 100%;
				}

				.textMore {
					font-weight: 400;
					font-size: 1.5rem;
					margin: 2rem 0;
					margin-right: 2rem;
				}
			}
		}
		.image {
			float: right;
		}
	}
}
